











































































import Logo from "../misc/Logo.vue";
import NumberCard from "../home/NumberCard.vue";
import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import { routerModule, repositoriesModule, authModule } from "@/store/modules";
import DoesNotExistScreen from "@/components/misc/DoesNotExistScreen.vue";
import { IGetRepositoryStatsResponse } from "../../shared";
import {
  getRepoStats,
  checkRepo,
  unlockRepo,
  pruneRepo,
  rebuildRepoIndex,
  RepoFunc
} from "@/store/api";

@Component({
  components: {
    NumberCard,
    DoesNotExistScreen
  }
})
export default class RepositoryOverviewComponent extends Mixins() {
  funcsLoading = new Array(4).fill(false);

  get anyFuncLoading() {
    return this.funcsLoading.find(loading => loading) || false;
  }

  get repoId(): string {
    return routerModule.params.repositoryId;
  }

  get repoData() {
    return repositoriesModule.getById(this.repoId);
  }

  get details() {
    if (!this.repoData) return [];
    return [
      { title: this.$t("repository.details.name"), value: this.repoData.name },
      {
        title: this.$t("repository.details.location"),
        value: this.repoData.location
      },
      {
        title: this.$t("repository.details.s3-access-key"),
        value: this.repoData.s3AccessKey
      }
    ];
  }

  stats: IGetRepositoryStatsResponse | null = null;

  @Watch("repoId", { immediate: true })
  async getStats() {
    const response = await getRepoStats(authModule.userId, Number(this.repoId));
    if (response.payload) {
      this.stats = response.payload;
    }
  }

  get funcs() {
    const execFunc = (func: RepoFunc) => async (idx: number) => {
      Vue.set(this.funcsLoading, idx, true);
      try {
        await func(authModule.userId, Number(this.repoId));
      } catch (err) {
        console.error(err);
      }
      Vue.set(this.funcsLoading, idx, false);
    };
    return [
      {
        name: this.$t("repo.check"),
        icon: "mdi-check",
        func: execFunc(checkRepo)
      },
      {
        name: this.$t("repo.unlock"),
        icon: "mdi-lock-open",
        func: execFunc(unlockRepo)
      },
      {
        name: this.$t("repo.prune"),
        icon: "mdi-spray-bottle",
        func: execFunc(pruneRepo)
      },
      {
        name: this.$t("repo.rebuild-index"),
        icon: "mdi-wrench",
        func: execFunc(rebuildRepoIndex)
      }
    ];
  }
}
